<template>
  <AppRow permission="USERS_ADMINISTRATION">
    <template v-slot>
      <v-col cols="12">
        <v-card
          flat
        >
          <v-card-text>
            <v-row>
              <v-col
                cols="12"
                sm="6"
              >
                <v-list-item
                  v-for="item of items"
                  :key="item.value"
                  two-line
                >
                  <v-list-item-content>
                    <v-list-item-title>{{ item.text }}</v-list-item-title>
                    <v-list-item-subtitle v-if="item.value === 'createdAt'">
                      {{ userData.getUserInfo[item.value] | moment('DD.MM.YYYY, HH:mm') }}
                    </v-list-item-subtitle>
                    <v-list-item-subtitle v-else>
                      {{ userData.getUserInfo[item.value] || 'Неизвестно' }}
                    </v-list-item-subtitle>
                  </v-list-item-content>
                </v-list-item>
              </v-col>
              <v-col
                cols="12"
                sm="6"
              >
                <v-list-item
                  v-for="item of secondItems"
                  :key="item.value"
                  two-line
                >
                  <v-list-item-content>
                    <v-list-item-title>{{ item.text }}</v-list-item-title>
                    <v-list-item-subtitle v-if="item.value === 'email'">
                      <v-chip v-if="userData.getUserInfo.emailVerified">
                        <v-icon left>
                          mdi-check
                        </v-icon>
                        {{ userData.getUserInfo[item.value] }}
                      </v-chip>
                      <template v-else>
                        {{ userData.getUserInfo[item.value] || 'Неизвестно' }}
                      </template>
                    </v-list-item-subtitle>
                    <v-list-item-subtitle v-else-if="item.value === 'socialAccounts'">
                      <a
                        v-for="link of links"
                        :key="link.value"
                        :href="userData.getUserInfo[item.value][link.value] ? `${link.link}${userData.getUserInfo[item.value][link.value]}` : ''"
                        :style="{ pointerEvents: userData.getUserInfo[item.value][link.value] ? '' : 'none' }"
                        target="_blank"
                      >
                        <v-icon :disabled="!userData.getUserInfo[item.value][link.value]">{{ link.icon }}</v-icon>
                      </a>
                    </v-list-item-subtitle>
                    <v-list-item-subtitle v-else>
                      {{ userData.getUserInfo[item.value] || 'Неизвестно' }}
                    </v-list-item-subtitle>
                  </v-list-item-content>
                </v-list-item>
              </v-col>
            </v-row>
          </v-card-text>
        </v-card>
      </v-col>

      <v-col
        cols="12"
        class="text-center text-h5"
      >
        История ставок
      </v-col>

      <v-col
        cols="12"
        sm="6"
      >
        <v-select
          v-model="gameMode"
          :items="gameModes"
          solo
          flat
          hide-details="auto"
          clearable
          background-color="#464646"
          label="Режим"
          class="rounded-lg"
          @change="firstPage"
        />
      </v-col>

      <v-col
        cols="12"
        sm="6"
      >
        <DatePicker
          label="Диапазон дат"
          @save="saveDates"
        />
      </v-col>

      <v-col cols="12">
        <v-card
          flat
          class="px-4 pb-2"
        >
          <v-card-title class="py-7">
            Ставки: 0
          </v-card-title>
          <v-data-table
            :headers="headers"
            :items="[]"
            :page.sync="page"
            :items-per-page="itemsPerPage"
            disable-sort
            no-data-text="История отсутствует"
            hide-default-footer
          >
            <template v-slot:[`item.createdAt`]="{ item }">
              {{ item.createdAt | moment('DD.MM.YYYY, HH:mm') }}
            </template>

            <template v-slot:[`item.win`]="{ item }">
              <v-simple-checkbox
                v-model="item.win"
                disabled
              />
            </template>
          </v-data-table>
          <v-divider />

          <div class="text-center pt-3">
            <v-pagination
              v-model="page"
              :total-visible="9"
              :length="Math.ceil(0 / itemsPerPage)"
            />

            <v-select
              v-model="itemsPerPage"
              :items="[5, 10, 15]"
              label="Ставок на странице"
              class="pa-4 pt-5"
              @change="firstPage"
            />
          </div>
        </v-card>
      </v-col>

      <v-col
        cols="12"
        class="text-center text-h5"
      >
        Информация об игроке
      </v-col>

      <v-col cols="12">
        <v-row>
          <v-col
            v-for="item of cards"
            :key="item.value"
            cols="12"
            sm="6"
          >
            <InfoCard
              :loading="0"
              :item="item"
              :data="userData"
              property="getUserInfo"
              icon="RUB"
            >
              <template v-if="item.value === 'balanceBonus'">
                <v-btn
                  icon
                  absolute
                  top
                  right
                  @click="show = !show"
                >
                  <v-icon>{{ show ? 'mdi-chevron-up' : 'mdi-chevron-down' }}</v-icon>
                </v-btn>
                <v-expand-transition>
                  <div v-show="show">
                    <v-divider />

                    <v-card-text>
                      <v-list-item
                        v-for="balance of balances"
                        :key="balance.value"
                        two-line
                      >
                        <v-list-item-content>
                          <v-list-item-title>{{ balance.text }}</v-list-item-title>
                          <v-list-item-subtitle>{{ userData ? userData.getUserInfo[balance.value] : '?' }}</v-list-item-subtitle>
                        </v-list-item-content>
                      </v-list-item>
                    </v-card-text>
                  </div>
                </v-expand-transition>
              </template>
            </InfoCard>
          </v-col>

          <v-col cols="12">
            <v-card
              flat
              class="px-4 pb-2"
            >
              <v-simple-table>
                <template v-slot:default>
                  <thead>
                    <tr>
                      <th />
                      <th class="white--text text-subtitle-2">
                        Количество:
                      </th>
                      <th class="white--text text-subtitle-2">
                        Сумма:
                      </th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr
                      v-for="item of stats"
                      :key="item.key"
                    >
                      <td>{{ item.name }}</td>
                      <td>{{ userData.getUserInfo[item.count] }}</td>
                      <td>{{ userData.getUserInfo[item.amount] }}</td>
                    </tr>
                  </tbody>
                </template>
              </v-simple-table>
            </v-card>
          </v-col>

          <v-col
            v-for="item of bets"
            :key="item.value"
            cols="12"
            :sm="item.value === 'ngr' ? '12' : '6'"
          >
            <InfoCard
              :loading="0"
              :item="item"
              :data="userData"
              property="getUserInfo"
              :icon="item.value === 'betsAmount' ? '' : 'RUB'"
            />
          </v-col>
        </v-row>
      </v-col>

      <v-col
        cols="12"
        class="text-center text-h5"
      >
        Уведомления
      </v-col>
      <v-col
        cols="12"
      >
        <v-card
          flat
          class="pa-4"
        >
          <v-row>
            <v-col
              cols="12"
              sm="6"
              md="4"
            >
              <v-text-field
                v-model="message"
                solo
                flat
                hide-details="auto"
                background-color="#464646"
                label="Сообщение"
                class="rounded-lg"
              />
            </v-col>
            <v-col
              cols="12"
              sm="6"
              md="4"
            >
              <v-text-field
                v-model="from"
                solo
                flat
                hide-details="auto"
                background-color="#464646"
                label="От"
                class="rounded-lg"
              />
            </v-col>
            <v-col
              cols="12"
              md="4"
            >
              <v-btn
                block
                depressed
                color="primary"
                class="text-none rounded-lg"
                height="48"
                x-large
              >
                Отправить
              </v-btn>
            </v-col>
          </v-row>
        </v-card>
      </v-col>

      <v-col
        cols="12"
        class="text-center text-h5"
      >
        Антифрод
      </v-col>

      <v-col cols="12">
        <v-select
          v-model="criterion"
          :items="criteria"
          solo
          flat
          hide-details="auto"
          clearable
          background-color="#464646"
          label="Список пользователей, попавших под подозрение по критериям"
          class="rounded-lg"
        >
          <template v-slot:prepend-inner>
            <v-icon class="mr-1">
              mdi-account-cancel
            </v-icon>
          </template>
        </v-select>
      </v-col>

      <v-col>
        <v-card
          :loading="false"
          flat
          class="px-4 pb-2"
        >
          <v-data-table
            :headers="headersAntifraud"
            :items="[]"
            :items-per-page="5"
            no-data-text="Пользователи отсутствуют"
          >
            <template v-slot:[`item.action`]>
              <v-btn
                depressed
                color="primary"
                class="text-none"
                large
                height="36"
              >
                Одобрить
              </v-btn>
            </template>
          </v-data-table>
        </v-card>
      </v-col>
    </template>
  </AppRow>
</template>

<script>
import AppRow from '@/components/AppRow.vue';
import DatePicker from '@/components/DatePicker.vue';
import InfoCard from '@/components/InfoCard.vue';

export default {
  components: {
    AppRow,
    DatePicker,
    InfoCard,
  },
  data() {
    return {
      user: {},
      items: [
        {
          text: 'Имя',
          value: 'username',
        },
        {
          text: 'Роль',
          value: 'role',
        },
        {
          text: 'Забанен',
          value: 'banned',
        },
        {
          text: 'Дата регистрации',
          value: 'createdAt',
        },
      ],
      secondItems: [
        {
          text: 'User ID',
          value: 'userId',
        },
        {
          text: 'Email',
          value: 'email',
        },
        {
          text: 'Номер телефона',
          value: 'mobilePhone',
        },
        {
          text: 'Привязанные соц. сети',
          value: 'socialAccounts',
        },
      ],
      links: [
        {
          link: 'https://vk.com/',
          icon: 'mdi-vk',
          value: 'vk',
        },
        {
          link: 'https://steamcommunity.com/profiles/',
          icon: 'mdi-steam',
          value: 'steam',
        },
        {
          link: 'https://t.me/',
          icon: 'mdi-telegram',
          value: 'telegram',
        },
      ],
      userData: {
        getUserInfo: {
          username: 'Alex',
          userId: '12',
          role: 'USER',
          banned: false,
          createdAt: 79362187454,
          email: 'test@gmailc.com',
          emailVerified: true,
          mobilePhone: '',
          socialAccounts: {
            vk: '',
            steam: 'testtest',
            telegram: '',
          },
          balanceReal: 100,
          balanceBonus: 0,
          promocodes: 0,
          bonusSlot: 0,
          moneyRain: 0,
          chatPrises: 0,
          depositCount: 1,
          depositAmount: 100,
          withdrawsCount: 0,
          withdrawsAmount: 0,
          referralWithdrawCount: 0,
          referralWithdrawAmount: 0,
          bonusCount: 0,
          bonusAmount: 0,
          ngr: 10,
          betsAmount: 1,
          depositWagering: 10,
        },
      },
      headers: [
        { text: 'ID:', value: 'id', class: 'white--text text-subtitle-2' },
        { text: 'Режим:', value: 'gameMode', class: 'white--text text-subtitle-2' },
        { text: 'Дата:', value: 'createdAt', class: 'white--text text-subtitle-2' },
        { text: 'Сумма:', value: 'betAmount', class: 'white--text text-subtitle-2' },
        { text: 'Результат:', value: 'result', class: 'white--text text-subtitle-2' },
        { text: 'Выиграл:', value: 'win', class: 'white--text text-subtitle-2' },
      ],
      page: 1,
      itemsPerPage: 5,
      gameMode: null,
      gameModes: ['UNDEFINED_GAME_MODE', 'CRASH', 'DICES', 'MINES', 'DOUBLE', 'TOWER', 'JACKPOT'],
      dateFrom: '',
      dateTo: '',
      cards: [
        {
          text: 'Реальный баланс',
          value: 'balanceReal',
        },
        {
          text: 'Бонусный баланс',
          value: 'balanceBonus',
        },
      ],
      show: false,
      balances: [
        {
          text: 'Промокоды',
          value: 'promocodes',
        },
        {
          text: 'Бонусный слот',
          value: 'bonusSlot',
        },
        {
          text: 'Викторина в чате',
          value: 'chatPrises',
        },
        {
          text: 'Денежный дождь',
          value: 'moneyRain',
        },
      ],
      stats: [
        {
          key: 'deposits',
          name: 'Депозиты',
          count: 'depositCount',
          amount: 'depositAmount',
        },
        {
          key: 'withdraws',
          name: 'Выводы',
          count: 'withdrawsCount',
          amount: 'withdrawsAmount',
        },
        {
          key: 'referralWithdraws',
          name: 'Реферальные выводы',
          count: 'referralWithdrawCount',
          amount: 'referralWithdrawAmount',
        },
        {
          key: 'bonuses',
          name: 'Бонусы',
          count: 'bonusCount',
          amount: 'bonusAmount',
        },
      ],
      bets: [
        {
          text: 'Доход (NGR)',
          value: 'ngr',
        },
        {
          text: 'Сделано ставок',
          value: 'betsAmount',
        },
        {
          text: 'Сумма отыгрыша депозита',
          value: 'depositWagering',
        },
      ],
      message: '',
      from: '',
      criterion: '',
      criteria: ['Совпадают кошельки депозитов/выводов на 2-х и более аккаунтах', 'Совпадает ip-адрес на 2-х и более аккаунтах', 'Совпадают регистрационные данные на 2-х и более аккаунтах', 'Совпадающий FingerPrint (отпечаток железа)'],
      headersAntifraud: [
        { text: 'ID:', value: 'id', class: 'white--text text-subtitle-2' },
        { text: 'Ник:', value: 'username', class: 'white--text text-subtitle-2' },
        { text: 'Баланс:', value: 'balance', class: 'white--text text-subtitle-2' },
        { text: 'Дата регистрации:', value: 'registrationAt', class: 'white--text text-subtitle-2' },
        { text: 'Депозитов:', value: 'deposits', class: 'white--text text-subtitle-2' },
        { text: 'Выводов:', value: 'withdraws', class: 'white--text text-subtitle-2' },
        { text: 'Причины:', value: 'reasons', class: 'white--text text-subtitle-2' },
        { text: '', value: 'action', align: 'center' },
      ],
    };
  },
  computed: {
    userId() {
      return this.$route.params.id;
    },
  },
  methods: {
    firstPage() {
      this.page = 1;
    },
    saveDates(dates) {
      [this.dateFrom, this.dateTo] = dates;
    },
    onNotification() {
      this.message = '';
      this.from = '';
    },
  },
};
</script>
